import "./App.css";
import { useEffect, useState } from "react";
import axios from "axios";
import { Button, Input, Table,Badge, Alert } from "reactstrap";
import { VscCheck, VscChromeClose } from "react-icons/vsc"
import ClientList from "./ClientList";
import LogList from "./LogList";
const CLOUD_WS = "wss://anaks-ws-server.herokuapp.com";
const LOCAL_WS = "ws://localhost:5005"
const CLOUD_HTTP = "https://anaks-ws-server.herokuapp.com"
const LOCAL_HTTP = "http://localhost:5005"
const socket = new WebSocket(CLOUD_WS);
function App() {
  const [clients, setClients] = useState([]);
  const [logs, setLogs] = useState([]);
  const [selectedClient, setSelectedClient] = useState({});
  const [data, setData] = useState("");
  const [me, setMe] = useState("");
  
  useEffect(() => {
    socket.onopen = (event) => {
      //console.log(event);
    };
    socket.onmessage = (event) => {
      console.log(event.data);
      try {
        const message = JSON.parse(event.data);
        if (message.type === "YOUR_ID") {
          setMe(message.socketID);
        }
      } catch (error) {
        console.log(error);
      }
      getClients();
      getLogs();
    };
  }, []);
  useEffect(() => {
    getClients();
    getLogs();
  }, [me]);
  
  const getClients = () => {
    axios
      .get(`${CLOUD_HTTP}/clients`)
      .then((res) => {
        setClients(res.data);
      })
      .catch(()=>alert("HTTP server connection error"));
  };
  const getLogs = () => {
    axios
      .get(`${CLOUD_HTTP}/logs`)
      .then((res) => {
        setLogs(res.data.reverse());
      })
      .catch(console.log);
  };
  const sendDataToSelectedClient = () => {
    if (!selectedClient._id) {
      alert('Firstly, select a client');
    }
    else {
      const msg = {
        type: 'SPECIFIC_CLIENT',
        client: selectedClient._id,
        index: selectedClient.index,
        data: data
      }
      socket.send(JSON.stringify(msg));
    }
    getLogs();
  }
  const sendDataToAllClients = () => {
    socket.send(JSON.stringify({
      type: 'ALL',
      client:'ALL',
      data:data
    }));
    getLogs();
  }
  const clearLogs = () => {
    axios
      .get(`${CLOUD_HTTP}/logs/clear`)
      .then((res) => {
        setLogs(res.data);
      })
      .catch(console.log);
  };
  return (
    <div style={{ padding: '20px' }}>
      <div>
        <Button style={{marginRight:'3px'}} color="warning" onClick={getClients}>Refresh Clients</Button>
        <Button color="warning" onClick={getLogs}>Refresh Logs</Button>
      </div>
      <div style={{display: 'flex', justifyContent: 'center', marginBottom: '5px', marginTop: '5px',position:'sticky',top:'10px',background:'white'}}>
        <Input style={{width:'40%'}} placeholder="enter data" onChange={(e) => setData(e.target.value)} />
        <Button style={{marginLeft:'5px',marginRight:'5px'}} color="success" outline onClick={sendDataToSelectedClient}>Send data to client <Badge color="success">{selectedClient.index}</Badge></Button>
        <Button color="primary" outline onClick={sendDataToAllClients}>Send data to all clients</Button>
      </div>
      <div style={{display:'flex',justifyContent:'space-around'}}>
        <ClientList clients={clients} selectedClient={selectedClient} setSelectedClient={setSelectedClient} me={me}/>
        <LogList logs={logs} clearLogs={clearLogs} me={me}/>       
      </div>
    </div>
  );
}

export default App;
