import React from "react";
import { VscCheck } from "react-icons/vsc";
import { Badge, Button, Table } from "reactstrap";

export default function ClientList({
  clients,
  selectedClient,
  setSelectedClient,
  me,
}) {
  return (
    <div>
      <h2>Client List ({clients.length})</h2>
      <div
        style={{
          border: "2px solid #6c757d",
          borderRadius: "4px",
          position: "sticky",
          top: "100px",
        }}
      >
        <Table borderless hover bordered>
          <thead>
            <tr>
              <th>Selected</th>
              <th>#</th>
              <th>Client ID</th>
              <th>Client Address</th>
            </tr>
          </thead>
          <tbody>
            {clients.map((client, index) => {
              return (
                <tr
                  style={{
                    background:
                      selectedClient._id === client._id &&
                      "rgba(123, 237, 159,0.6)",
                  }}
                  key={client._id}
                  className="table-tr"
                  onClick={() =>
                    setSelectedClient({ index: index + 1, ...client })
                  }
                >
                  <td>
                    {selectedClient._id === client._id && (
                      <VscCheck
                        style={{
                          fontSize: "24px",
                          color: "green",
                          textAlign: "center",
                          width: "100%",
                        }}
                      />
                    )}
                  </td>
                  <th scope="row">
                    {index + 1}{" "}
                    {client._id === me ? <Badge color="success">Me</Badge> : ""}
                  </th>
                  <td>{client._id}</td>
                  <td>{client.address}</td>
                </tr>
              );
            })}
          </tbody>
        </Table>
      </div>
    </div>
  );
}
