import React from "react";
import { Badge, Button, Table } from "reactstrap";
export default function LogList({ logs, clearLogs,me }) {
  return (
    <div>
      <div style={{ display: "flex", alignItems: "center" }}>
        <h2>Log List</h2>
        <Button
          outline
          color="danger"
          style={{ marginLeft: "10px" }}
          onClick={clearLogs}
        >
          Clear ({logs.length})
        </Button>
      </div>
      <div style={{ border:'2px solid #6c757d',borderRadius:'4px' }}>
        <Table bordered striped>
          <thead>
            <tr>
              <th>Type</th>
              <th>Data</th>
              <th>From</th>
              <th>To</th>
              <th>Timestamp</th>
            </tr>
          </thead>
          <tbody>
            {logs.map((log, index) => {
              return (
                <tr key={index} className="table-tr">
                  <td>
                    <Badge color={log.type === "ALL" ? "success" : "primary"}>
                      {log.type}
                    </Badge>
                  </td>
                  <td>{log.data}</td>
                      <td style={{ color: "blue" }}>{log.owner} {log.owner === me && <Badge color="success">Me</Badge>}</td>
                  <td style={{ color: "green" }}>{log.client}</td>
                  <td>{log.timestamp}</td>
                </tr>
              );
            })}
          </tbody>
        </Table>
      </div>
    </div>
  );
}
